@charset "UTF-8";
body {
  font-family: "Noto Sans JP", sans-serif;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4814814815vw;
  }
}
@media (min-width: 1080px) {
  html {
    font-size: 16px;
  }
}

/* ============================================
   RESET CSS + アクセシビリティ対応
   ============================================ */
/* 全要素を border-box に統一（余計なサイズ計算を避ける） */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* デフォルトの余白をリセット */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

ul,
ol {
  padding: 0;
  list-style: none; /* 必要な場合は個別に list-style を戻す */
}

/* スクロール挙動を滑らかに（ユーザー体験改善） */
html {
  scroll-behavior: smooth;
}

/* 本文の基本設定 */
body {
  min-height: 100vh;
  text-rendering: optimizeLegibility; /* 字形を見やすく */
  line-height: 1.5; /* 読みやすい行間 */
  font-family: system-ui, sans-serif;
}

/* リンクの基本リセット */
a {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}

/* ホバー・アクティブ状態 */
a:hover,
a:active {
  color: inherit;
  text-decoration: none;
}

/* フォーカス状態（アクセシビリティ） */
a:focus-visible {
  outline: 2px solid #3498db;
  outline-offset: 2px;
}

/* 画像は親要素の幅にフィットさせる */
img {
  max-width: 100%;
  display: block;
  height: auto;
}

/* 記事内要素の間隔を自動で確保 */
article > * + * {
  margin-top: 1em;
}

/* ============================================
   フォーム要素リセット
   ============================================ */
/* フォントを継承・不要なデフォルト装飾を外す */
input,
button,
select,
textarea {
  font: inherit;
  background: transparent;
  border: none;
  border-radius: 0;
  padding: 0;
  margin: 0;
}

/* appearance: none は必要なものだけに限定 */
.input-text,
.input-email,
.input-tel,
.textarea-box {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

/* フォーカスリングは必ず残す（outline: none は禁止） */
:focus-visible {
  outline: 2px solid #3498db; /* キーボード操作用の青枠 */
  outline-offset: 2px;
}

/* ボタンやセレクトボックスはカーソルを pointer に */
input[type=submit],
input[type=button],
button,
select,
label {
  cursor: pointer;
}

/* 無効状態をわかりやすく */
input[disabled],
button[disabled] {
  cursor: not-allowed;
  opacity: 0.6;
}

/* テキストエリアは縦方向のみリサイズ可能に */
textarea {
  resize: vertical;
}

/* ============================================
   アクセシビリティ対応
   ============================================ */
/* スキップリンク（キーボードユーザー用） */
.skip-link {
  position: absolute;
  top: -40px;
  left: 6px;
  background: #000;
  color: #fff;
  padding: 8px;
  text-decoration: none;
  z-index: 1000;
}

.skip-link:focus {
  top: 6px;
}

/* 必須フィールドをラベルにマーク表示 */
label.required::after {
  content: " *";
  color: #e74c3c;
}

/* エラーメッセージのスタイル */
.error-message {
  color: #e74c3c;
  font-size: 0.9em;
}

/* エラー状態は入力中にだけ強調 */
input:invalid:focus,
textarea:invalid:focus,
select:invalid:focus {
  border: 2px solid #e74c3c;
}

/* ラジオ・チェックボックスは視覚的に隠すが、スクリーンリーダーでは有効 */
input[type=radio],
input[type=checkbox] {
  position: absolute;
  opacity: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0 0 0 0);
  white-space: nowrap;
  border: 0;
}

/* 代替UI（label側）にフォーカスリングを表示 */
input[type=radio]:focus + label,
input[type=checkbox]:focus + label {
  outline: 2px solid #3498db;
  outline-offset: 2px;
}

/* =========================================================
  Hamburger
  （ハンバーガーボタン）
========================================================= */
.hamburger {
  width: 2.625rem;
  height: 0.875rem;
  position: relative;
  z-index: 1001;
  cursor: pointer;
}
@media screen and (min-width: 768px) {
  .hamburger {
    width: 3.5rem;
    height: 0.875rem;
  }
}

/* ----------------------------------------------
  線（2本）
---------------------------------------------- */
.hamburger__line01,
.hamburger__line02 {
  display: block;
  height: 2px;
  background-color: #6F8471;
  transition: all 0.4s ease;
  position: absolute;
  right: 0;
}

/* 1本目（上の線） */
.hamburger__line01 {
  width: 100%;
  top: 0;
}

/* 2本目（下の線） */
.hamburger__line02 {
  width: 71.4285714286%;
  bottom: 0;
}
@media screen and (min-width: 768px) {
  .hamburger__line02 {
    width: 71.4285714286%;
  }
} /* ----------------------------------------------
  アクティブ時（is-active）
---------------------------------------------- */
.hamburger.is-active .hamburger__line01 {
  transform: translateY(0.25rem) rotate(18deg);
  width: 100%;
}
@media screen and (min-width: 768px) {
  .hamburger.is-active .hamburger__line01 {
    transform: translateY(0.5rem) rotate(18deg);
  }
}

.hamburger.is-active .hamburger__line02 {
  transform: translateY(-0.5625rem) rotate(-18deg);
  width: 100%;
}
@media screen and (min-width: 768px) {
  .hamburger.is-active .hamburger__line02 {
    transform: translateY(-0.25rem) rotate(-18deg);
  }
}

/* アクティブ時の色変更 */
body.is-fixed {
  overflow: hidden;
  height: 100%;
}

.header-nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: #ffffff;
  opacity: 0;
  visibility: hidden;
  transition: all 0.4s ease;
  z-index: 1000;
  overflow: hidden; /* ← 初期状態ではスクロール無効 */
}
@media screen and (min-width: 768px) {
  .header-nav {
    right: 0;
    left: auto;
  }
}

/* 開いたとき（ハンバーガークリックでis-active付与） */
.header-nav.is-active {
  opacity: 1;
  visibility: visible;
  overflow-y: auto; /* ← 開いたときに内部だけスクロール可能に */
  -webkit-overflow-scrolling: touch; /* ← iOS滑らかスクロール対応 */
}

.header-nav__list {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
  text-align: center;
}

.header-nav__en {
  font-family: "Cormorant Garamond", serif;
  font-size: 1.25rem;
  color: #151515;
  font-weight: 400;
  line-height: 1.5;
}

.header-nav__ja {
  display: block;
  font-family: "Noto Sans JP", sans-serif;
  font-size: 0.625rem;
  color: #151515;
  font-weight: 400;
  line-height: 1;
}

.header-nav__sns {
  display: flex;
  gap: 2.5rem;
  justify-content: center;
  margin-top: 2.5rem;
}

.header-nav__sns-item {
  width: 1.25rem;
  height: 1.25rem;
  aspect-ratio: 20/20;
}

.header {
  margin-top: 1.1875rem;
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 0;
}

.header__logo {
  display: block;
  width: 6.5625rem;
  height: 1.6875rem;
  aspect-ratio: 105/27;
}
@media screen and (min-width: 768px) {
  .header__logo {
    width: 8.25rem;
    height: 2.625rem;
    aspect-ratio: 132/42;
  }
}

.header__logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.header__nav {
  max-width: 23.4375rem;
  margin-top: 7.5rem;
}

.inner {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
@media screen and (min-width: 768px) {
  .inner {
    max-width: 1130px;
    padding-right: 25px;
    padding-left: 25px;
  }
}